const errorTranslation = {
	"any.required": "",
	"string.empty": "",
	"string.uri": "URL ",
	"string.min": (errorObj) =>
		` ${errorObj.context.limit} `,
	"string.max": (errorObj) =>
		` ${errorObj.context.limit} `,
	"string.email": "",
	"number.base": "",
};

export const getErrorMessage = (errorObj, label) => {
	label = label || errorObj?.context?.label;

	var { message, type, context } = errorObj;
	if (type === "any.custom") return context?.error?.message;
	if (/[-]/.test(message.replace(/"[^"]+"/g, ""))) return message;
	if (/pattern/.test(type) && label) return `${label}`;

	var found = errorTranslation[type];
	if (!found && label) return `${label} (${message})`;
	if (!found) return ` (${message})`;

	if (typeof found === "string") return found;
	return found(errorObj);
};
